.history-header {
  position: relative;
  overflow: hidden;

  &-row {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 50px;
    margin-left: 0;
    margin-right: 0;
  }
  &-top {
    padding-top: 130px;

    @media (max-width: 1439px) {
      padding-top: 115px;
    }
  }
  &-bottom {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    flex: 0 0 100%;
  }
  &-btn {
    flex: 0 0 170px;
    max-width: 170px;
  }
  &-titles {
    max-width: 1030px;

    @media (max-width: 1439px) {
      max-width: 630px;
    }
  }
  &-text {
    @include fz(42);
    margin-bottom: 30px;
    line-height: 1.1;

    @media (max-width: 1439px) {
      @include fz(32);
    }
  }

  &-bg {
    position: absolute;
    z-index: -1;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
    object-position: center;
  }

  &-arrow {
    display: inline-block;
    width: 34px;
    height: 13px;
    cursor: pointer;

    svg {
      fill: none;
      stroke: #FFF;
      stroke-width: 2;
    }
  }
}


.ui-top-screen {
  &-title {
    max-width: 920px;
  }

  &-text {
    max-width: 1035px;
    @include fz(42);
    margin-bottom: 44px;
  }
}

.history-upper {
  text-transform: uppercase;
}

.history-body {
  padding-top: 100px;
  color: #000;
  @include fz(22);

  @media (max-width: 1439px) {
    padding-top: 50px;
    @include fz(18);
  }
}
.history-tabs {
  font-size: 0;

  li {
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 15px;
    margin-right: 16px;
  }

  &-link {
    display: inline-block;
    vertical-align: middle;
    height: 78px;
    line-height: 76px;
    border: 1px dashed $secondary;
    border-radius: 40px;
    padding: 0px 32px;
    color: $secondary;
    @include fz(42);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: color .3s, border .3s;

    &:hover,
    &.active {
      color: #000;
      border: 1px solid #000;
    }
  }

  @media (max-width: 1439px) {
    &-link {
      @include fz(32);
      height: 72px;
      line-height: 70px;
    }
  }
  @media (max-width: 1259px) {

    &-link {
      @include fz(28);
      height: 68px;
      line-height: 66px;
    }
  }
}

